.Result {
    display: flex;
    flex-direction: row;
    margin: 15px;
    width: 60vw;
    height: 100px;
    border-radius: 30px;
    background: #e0e0e0;
    box-shadow: 15px 15px 30px #bebebe,
                -15px -15px 30px #ffffff;

    align-items:center;
    
    justify-content: space-evenly;
}
h2{
    font-size: xx-large;
}
.Result p:first-child {
    border: none;
}
.Result p {
    
    font-size:x-large;
}